import React, { useState, useEffect } from "react";
import Sidebar from "../../../components/Admin/Sidebar";
import TopBar from "../../../components/Admin//TopBar";
import { useNavigate, useParams } from "react-router-dom";
import axios from "axios";
import { Link } from "react-router-dom";
import DataTable from "react-data-table-component";
import { FaPlus } from "react-icons/fa6";
import { Modal, Button } from "react-bootstrap";
import { FaArrowLeft, FaEye, FaTrashAlt, FaEdit } from "react-icons/fa";
import SuccessAlert from "../../../components/Admin/SuccessAlert";
function AdminDiscountViewDetail() {
  const navigate = useNavigate();
  const [records, setRecords] = useState([]);
  const [getDataroompay, setgetDataroompay] = useState("");
  const { code } = useParams();
  useEffect(() => {
    document.title = "Amount Add DataRoom, Extra Due Diligence - Admin";
  }, []);
  const apiUrl = "https://blueprintcatalyst.com/api/admin/module/";
  const [searchQuery, setSearchQuery] = useState("");
  const [DiscountCode, setDiscountCode] = useState([]);
  // Define filtered data based on the search query
  const filteredRecords = records.filter((record) =>
    Object.values(record)
      .join(" ")
      .toLowerCase()
      .includes(searchQuery.toLowerCase())
  );
  useEffect(() => {
    getDataroompayment();
  }, []);
  const getDataroompayment = async () => {
    let formData = {
      user_id: "",
    };
    try {
      const res = await axios.post(apiUrl + "getDataroompayment", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data.row;
      console.log(respo);
      setgetDataroompay(respo[0]);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const [successMessage, setsuccessMessage] = useState("");
  const [show, setShow] = useState(false);
  const [Deleteid, setDeleteid] = useState("");
  const handleDelete = async (id) => {
    setDeleteid(id);
    setShow(true);
  };
  const columns = [
    {
      name: "Company Name",
      selector: (row) => row.company_name,
      sortable: true,
      className: "age",
    },
    {
      name: "Company Email",
      selector: (row) => row.email,
      sortable: true,
      className: "age",
    },
    {
      name: "Code",
      selector: (row) => row.code,
      sortable: true,
      className: "age",
    },
    {
      name: "Payment Type",
      selector: (row) => row.type,
      sortable: true,
      className: "age",
    },

    {
      name: "Discount",
      selector: (row) => row.used_discounts + "%",
      sortable: true,
      className: "age",
    },
    {
      name: "Actual Discount",
      selector: (row) => row.percentage + "%",
      sortable: true,
      className: "age",
    },
    {
      name: "Usege Limit",
      selector: (row) => row.usage_limit,
      sortable: true,
      className: "age",
    },
    {
      name: "Used",
      selector: (row) => row.used_count,
      sortable: true,
      className: "age",
    },
  ];

  useEffect(() => {
    getdiscountCode();
  }, []);
  const getdiscountCode = async () => {
    let formData = {
      code: code,
    };
    try {
      const res = await axios.post(apiUrl + "getUseddiscountCode", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data.results;
      setRecords(respo);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const handleClose = () => {
    setShow(false);
  };
  const handleConfirmDelete = async () => {
    let formData = {
      id: Deleteid,
    };
    try {
      const res = await axios.post(apiUrl + "deletediscountcode", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data;
      setShow(false);
      getdiscountCode();
      setsuccessMessage(respo.message);
      setTimeout(() => {
        setsuccessMessage("");
      }, 1200); // 3
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  return (
    <>
      <div>
        <div className="d-flex align-items-start gap-0">
          <Sidebar />
          <div className="d-flex flex-column gap-0 w-100">
            <TopBar />
            <section className="dashboard_adminh">
              <div className="container-xl">
                <div className="row gy-4">
                  <div className="col-12">
                    <div className="card p-4">
                      {successMessage && (
                        <SuccessAlert
                          message={successMessage}
                          onClose={() => setsuccessMessage("")}
                        />
                      )}
                      <div className="d-flex justify-content-between mb-3">
                        <Link
                          to={`/admin/setting/paymentdiscountlist`}
                          className="btn btn-secondary py-2 my-3"
                          style={{ width: "fit-content" }}
                        >
                          <FaArrowLeft /> Back
                        </Link>
                        <div className="d-flex justify-content-between mb-3">
                          <h5 className="mb-3">Discount Code Used List</h5>
                        </div>
                      </div>
                      <div className="d-flex justify-content-end align-items-end">
                        <div class="search-bar">
                          <input
                            type="text"
                            placeholder="Search..."
                            value={searchQuery}
                            onChange={(e) => setSearchQuery(e.target.value)}
                          />
                          <span class="search-icon">
                            <svg
                              xmlns="http://www.w3.org/2000/svg"
                              fill="none"
                              viewBox="0 0 24 24"
                              stroke="currentColor"
                            >
                              <path
                                stroke-linecap="round"
                                stroke-linejoin="round"
                                stroke-width="2"
                                d="M21 21l-4.35-4.35M17 11a6 6 0 11-12 0 6 6 0 0112 0z"
                              />
                            </svg>
                          </span>
                        </div>
                      </div>
                      <DataTable
                        columns={columns}
                        data={filteredRecords}
                        pagination
                        highlightOnHover
                        striped
                        responsive
                        noDataComponent={
                          <div className="text-center">
                            <span>No results found</span>
                          </div>
                        }
                      />
                    </div>
                  </div>
                </div>
              </div>
            </section>
          </div>
        </div>
      </div>
      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Confirm Deletion</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <p>Are you sure you want to delete this record?</p>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Cancel
          </Button>
          <Button variant="danger" onClick={handleConfirmDelete}>
            Confirm Delete
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
}

export default AdminDiscountViewDetail;
